import React from 'react';
import cls from './Actions.module.css'
import Zoom from 'react-reveal/Zoom';

const Actions = () => {
    return (
        <section className={cls.flexContainer}>
            <div className={cls.infoContainer}>
                <Zoom bottom>
                    <div className={cls.infoArea}>
                        <div className={cls.infoTitle}>
                            <h3><i className="iStyle fa fa-2x fa-gift"/><br/>    
                            </h3>
                        </div>
                        <div className={cls.infoContent}>
                            <ul>
                                <li><span>
                                  170  -  10%
                        </span></li>
                                <li><span>
                                     340  -  20%
                        </span></li>
                            </ul>
                        </div>
                    </div>
                    <div className={cls.infoArea}>
                        <div className={cls.infoTitle}>
                            <h3><i className="iStyle fa fa-2x fa-percent"/><br/>    
                                </h3>
                        </div>
                        <div className={cls.infoContent}>
                            <ul>
                                <li><span>
                              150      2%
                       </span></li>
                                <li><span>
                                    36%
                        </span></li>
                            </ul>
                        </div>
                    </div>
                </Zoom>
            </div>
        </section>
    )
};

export default Actions;